import React, { useEffect } from "react";
import { Divider } from "antd";
import {
  DefaltSubtitle,
  WEDDING_PINK
} from '../../config'
import styled from "styled-components";
import Flower from "../assets/flower2.png";

const Wrapper = styled.div`
  padding-top: 42px;
  width: 80%;
  margin: 0 auto;
`;

const Title = styled.span`
  font-size: 1rem;
  font-family: MapoGoldenPier;
  color: var(--title-color);
  font-weight: bold;
  opacity: 0.85;
  margin-bottom: 0;
`;

const Image = styled.img`
  display: block;
  margin: 0 auto;
  width: 1.375rem;
  padding-bottom: 42px;
`;

const Content = styled.p`
  font-size: 0.875rem;
  font-family: GowunDodum;
  font-weight: 700;
  line-height: 1.75;
  opacity: 0.75;
  width: 100%;
  text-align: center;
  padding-bottom: 20px;
  margin: 0;
`;

const Map = styled.div`
  width: 100%;
  padding: 0;
`;

const Location = () => {
  //   

  // <!-- 3.   -->
  const executeScript = () => {
    const scriptTag = document.createElement("script");
    const inlineScript = document.createTextNode(`new daum.roughmap.Lander({
		"timestamp" : "1722501217034",
		"key" : "2k8qv",
		"mapWidth" : "640",
		"mapHeight" : "360"
  }).render();`);
    scriptTag.appendChild(inlineScript);
    document.body.appendChild(scriptTag);
  };

  // <!-- 2.   *    2   ,    . -->
  // document.write       
  const InstallScript = () => {
    (function () {
      let c = window.location.protocol === "https:" ? "https:" : "http:";
      let a = "16137cec";

      if (window.daum && window.daum.roughmap && window.daum.roughmap.cdn) {
        return;
      }
      window.daum = window.daum || {};
      window.daum.roughmap = {
        cdn: a,
        URL_KEY_DATA_LOAD_PRE: c + "//t1.daumcdn.net/roughmap/",
        url_protocal: c,
      };
      let b =
        c +
        "//t1.daumcdn.net/kakaomapweb/place/jscss/roughmap/" +
        a +
        "/roughmapLander.js";

      // document.write -> doumnet.body.append 
      const scriptTag = document.createElement("script");
      scriptTag.src = b;
      document.body.append(scriptTag);
      scriptTag.onload = () => {
        executeScript();
      };
    })();
  };

  useEffect(() => {
    InstallScript();
  }, [InstallScript]);

  return (
    <Wrapper>
      <Divider plain style={{ marginTop: 0, marginBottom: 32 }}>
        <DefaltSubtitle> LOCATION </DefaltSubtitle>
      </Divider>
      <Content style={{}}>
            132
        <br />
           
        <br />
        15 
        <br />
        <br />
        <p style={{fontSize: '0.6rem'}}>  /       
          <br />     .</p>
        <br />
        <br />
      </Content>
      <Map
        id="daumRoughmapContainer1722501217034"
        className="root_daum_roughmap root_daum_roughmap_landing"
      >
      </Map>
      <Content>
        <br />
        <DefaltSubtitle style={{color:"#d97d83"}}>BUS</DefaltSubtitle>
         : 13-1 
        <br />
          : 80 81 3002 4000 4500 
        <br />
        <br />
        <DefaltSubtitle style={{color:"#d97d83"}}>SUBWAY</DefaltSubtitle>
          
        <br />
        2  ( 5)
      </Content>
    </Wrapper>
  );
};

export default Location;
