import { Button, Divider, message, Modal } from "antd";
import React, { useState } from "react";
import CopyToClipboard from "react-copy-to-clipboard";
import styled from "styled-components";
import {
  BRIDE_ACCOUNT_BANK,
  BRIDE_ACCOUNT_NUMBER,
  BRIDE_FATHER_ACCOUNT_BANK,
  BRIDE_FATHER_ACCOUNT_NUMBER,
  BRIDE_FATHER_NAME,
  BRIDE_MOTHER_ACCOUNT_BANK,
  BRIDE_MOTHER_ACCOUNT_NUMBER,
  BRIDE_MOTHER_NAME,
  BRIDE_NAME,
  DefaltSubtitle,
  GROOM_ACCOUNT_BANK,
  GROOM_ACCOUNT_NUMBER,
  GROOM_FATHER_ACCOUNT_BANK,
  GROOM_FATHER_ACCOUNT_NUMBER,
  GROOM_FATHER_NAME,
  GROOM_MOTHER_ACCOUNT_BANK,
  GROOM_MOTHER_ACCOUNT_NUMBER,
  GROOM_MOTHER_NAME,
  GROOM_NAME
} from "../../config";
import AccountImg from "../assets/accountImg.png";
import AccountImg2 from "../assets/accountImg2.png";

const Wrapper = styled.div`
  font-family:mom_to_daughter;
  padding-top: 42px;
  padding-bottom: 18px;
  width: 90%;
  margin: 0 auto;
  text-align: center;
`;

const Title = styled.p`
  font-size: 1.5rem;
  color: var(--title-color);
  font-weight: bold;
  opacity: 0.85;
  margin-bottom: 0;
`;

const Content = styled.p`
  font-family: GowunDodum;
  font-size: 12px;
  line-height: 1.75;
  opacity: 0.75;
  margin-bottom: 6px;
`;

const SubContent = styled.p`
  font-family:GowunDodum;
  font-size: 0.875rem;
  line-height: 1.75;
  opacity: 0.75;
  margin-bottom: 42px;
`;

const Description = styled.p`
  font-family: GowunDodum;
  font-size: 0.875rem;
  line-height: 1.75;
  opacity: 0.65;
  margin-top: 8px;
`;

const ButtonWrap = styled.div`
  margin-bottom: 3.125rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
`;
const ContactButton = styled.div`
  width: 180px;
  height: 260px;
  border: 1px solid #efddde;
  padding: 2.188rem 0;
`;

const Image = styled.img`
  display: block;
  margin: 0 auto;
  width: 1.375rem;
  padding-bottom: 42px;
`;

const ImageAccount = styled.img`
  display: block;
  margin: 0 auto;
  width: 160px;
  padding-bottom: 0px;
`;

const Letter2 = styled.div`
  font-family: GowunDodum;
`;
const CongratulatoryMoney = () => {
  const [groomVisible, setGroomVisible] = useState(false);
  const [brideVisible, setBrideVisible] = useState(false);

  return (
    <Wrapper>

      <Divider  data-aos="fade-up"
                plain>
        <DefaltSubtitle> CONGRATULATION </DefaltSubtitle>
      </Divider>

      <Letter2 style={{}} data-aos="fade-up">
          
      <br />
         .
      <br />
          
      <br />
        .
      </Letter2>
      <br />
      <br />

      <ButtonWrap>
        <ContactButton data-aos="fade-up" onClick={() => setGroomVisible(true)}>
          <ImageAccount src={AccountImg} />
          <br />
          <SubContent> </SubContent>
        </ContactButton>
        <ContactButton data-aos="fade-up" onClick={() => setBrideVisible(true)}>
          <ImageAccount src={AccountImg2} />
          <br />
          <SubContent> </SubContent>
        </ContactButton>
      </ButtonWrap>
      <Modal
        title={<b style={{fontFamily:"GowunDodum"}}> </b>}
        visible={groomVisible}
        onOk={() => setGroomVisible(false)}
        onCancel={() => setGroomVisible(false)}
        footer={[
          <Description>
             ,    .
          </Description>,
        ]}
      >
        <div style={{fontFamily:"GowunDodum"}}>
           : {GROOM_FATHER_NAME}
          <Divider type="vertical" />
          {GROOM_FATHER_ACCOUNT_BANK} <CopyToClipboard text={GROOM_FATHER_ACCOUNT_NUMBER}>
            <Button
              type="text"
              style={{ padding: 0, margin: 0 }}
              onClick={() => message.success(" .")}
            >
              {GROOM_FATHER_ACCOUNT_NUMBER}
            </Button>
          </CopyToClipboard>
        </div>
        <div style={{ marginTop: 24, marginBottom: 24 ,fontFamily:"GowunDodum"}}>
           : {GROOM_MOTHER_NAME}
          <Divider type="vertical" />
          {GROOM_MOTHER_ACCOUNT_BANK } <CopyToClipboard text={GROOM_MOTHER_ACCOUNT_NUMBER}>
            <Button
              type="text"
              style={{ padding: 0, margin: 0 }}
              onClick={() => message.success(" .")}
            >
              {GROOM_MOTHER_ACCOUNT_NUMBER}
            </Button>
          </CopyToClipboard>
        </div>
        <div style={{fontFamily: "GowunDodum"}}>
           {GROOM_NAME}
          <Divider type="vertical" />
          {GROOM_ACCOUNT_BANK} <CopyToClipboard text={GROOM_ACCOUNT_NUMBER}>
            <Button
              type="text"
              style={{ padding: 0, margin: 0 }}
              onClick={() => message.success(" .")}
            >
              {GROOM_ACCOUNT_NUMBER}
            </Button>
          </CopyToClipboard>
        </div>
      </Modal>
      <Modal
        title={<b style={{fontFamily:"GowunDodum"}}> </b>}
        visible={brideVisible}
        onOk={() => setBrideVisible(false)}
        onCancel={() => setBrideVisible(false)}
        footer={[
          <Description>
             ,    .
          </Description>,
        ]}
      >
        <div style={{fontFamily: "GowunDodum"}}>
           : {BRIDE_FATHER_NAME}
          <Divider type="vertical" />
          {BRIDE_FATHER_ACCOUNT_BANK} <CopyToClipboard text={BRIDE_FATHER_ACCOUNT_NUMBER}>
            <Button
              type="text"
              style={{ padding: 0, margin: 0 }}
              onClick={() => message.success(" .")}
            >
              {BRIDE_FATHER_ACCOUNT_NUMBER}
            </Button>
          </CopyToClipboard>
        </div>
        <div style={{ marginTop: 24, marginBottom: 24 ,fontFamily:"GowunDodum"}}>
           : {BRIDE_MOTHER_NAME}
          <Divider type="vertical" />
          {BRIDE_MOTHER_ACCOUNT_BANK} <CopyToClipboard text={BRIDE_MOTHER_ACCOUNT_NUMBER}>
            <Button
              type="text"
              style={{ padding: 0, margin: 0}}
              onClick={() => message.success(" .")}
            >
              {BRIDE_MOTHER_ACCOUNT_NUMBER}
            </Button>
          </CopyToClipboard>
        </div>
        <div style={{fontFamily: "GowunDodum"}}>
           {BRIDE_NAME}
          <Divider type="vertical" />
          {BRIDE_ACCOUNT_BANK} <CopyToClipboard text={BRIDE_ACCOUNT_NUMBER}>
            <Button
              type="text"
              style={{ padding: 0, margin: 0 }}
              onClick={() => message.success(" .")}
            >
              {BRIDE_ACCOUNT_NUMBER}
            </Button>
          </CopyToClipboard>
        </div>
      </Modal>
    </Wrapper>
  );
};

export default CongratulatoryMoney;
