import React from "react";
import styled from "styled-components";
import { Divider } from "antd";
import {
  GROOM_NAME,
  GROOM_FATHER_NAME,
  GROOM_MOTHER_NAME,
  BRIDE_NAME,
  BRIDE_FATHER_NAME,
  BRIDE_MOTHER_NAME,
  WEDDING_BLACK,
  CONTENT_BLACK,
  WEDDING_PINK
} from "../../config";
import Flower from "../assets/flower1.png";

const Wrapper = styled.div`
  padding-top: 42px;
  margin: 0 auto;
  width: 90%;
`;

const Title = styled.p`
  font-size: 1rem;
  font-family: Kopuro;
  color: ${WEDDING_PINK};
  letter-spacing: 2.5px;
  font-weight: 300;
  opacity: 0.85;
  margin-bottom: 0;
  text-align: center;
`;

const Content = styled.p`
  font-size: 13px;
  font-family: GowunDodum;
  color:${CONTENT_BLACK};
  line-height: 2;
  opacity: 1.0;
  margin-bottom: 16px;
  width: 100%;
  text-align: center;
`;

const GroomBride = styled.p`
  font-family: "GowunDodum";
  font-size: 18px;
  line-height: 1.75;
  opacity: 0.85;
  margin-bottom: 0px;
  width: 100%;
  text-align: center;
  white-space: nowrap;
`;

const Image = styled.img`
  display: block;
  margin: 0 auto;
  width: 1.375rem;
  padding-bottom: 42px;
`;

const GroomName = styled.span`
  font-weight: bold;
  font-family: "GowunDodum";
`;

const Greeting = () => {
  return (
    <Wrapper>
      <Divider style={{ marginTop: 50, marginBottom: 8 }} plain>
        <Title data-aos="fade-up" >INVITATION</Title>
      </Divider>
      <Content data-aos="fade-up">
             
        <br />
        ,      .
        <br />
               
        <br />
           .
        <br />
              .
      </Content>
      <GroomBride data-aos="fade-up">
        {GROOM_FATHER_NAME}  {GROOM_MOTHER_NAME}   <GroomName>{GROOM_NAME}</GroomName>
        <br />
        {BRIDE_FATHER_NAME}  {BRIDE_MOTHER_NAME}   <GroomName>{BRIDE_NAME}</GroomName>
      </GroomBride>
    </Wrapper>
  );
};

export default Greeting;
