import { Layout } from "antd";
import "antd/dist/antd.css";
import React, { useEffect } from "react";
import "react-image-gallery/styles/css/image-gallery.css";
import styled from "styled-components";
import Gallery from "../components/gallery";
import Greeting from "../components/greeting";
import Title from "../components/title";
import "../styles/index.css";

import GroovePaper from "../assets/GroovePaper.png";
import CongratulatoryMoney from "../components/congratulatoryMoney";
import Kakao from "../components/kakao";
import Location from "../components/location";

import AOS from "aos";
import "aos/dist/aos.css";
import { Helmet, HelmetProvider } from "react-helmet-async";
import FixedAudioControl from "../components/Background";

// markup
const { Footer } = Layout;

const Wrapper = styled.div`
  background: #FDF8F8;
  background-image: url(${GroovePaper});
  width: 100%;
`;

const IndexPage = () => {
  useEffect(() => {
    const script = document.createElement("script");
    script.async = true;
    script.src = "https://developers.kakao.com/sdk/js/kakao.min.js";
    document.body.appendChild(script);

    return () => {
      document.body.romoveChile(script);
    };
  }, []);

  useEffect(() => {
    AOS.init({
      duration: 1500,
    });
  });
  return (
    <HelmetProvider >

    <Helmet>
    <meta charSet="UTF-8" />
          <meta name="viewport" content="width=device-width, initial-scale=1.0" />
          <meta property="og:title" content=" &   ." />
          <meta property="og:description" content="2024.11.09. ()  3:00   " />
          <meta property="og:image" content="https://github.com/user-attachments/assets/0ce8b78b-bc43-4c2f-8d34-7a648cc4e688" />
          <meta property="og:url" content="https://20190511.github.io/" />
          <meta property="og:type" content="website" />
          <title></title>
    </Helmet>
    <main>
    <Wrapper>
      <Kakao />
      <FixedAudioControl />
      <Title />
      <Greeting />
      <Gallery />
      <Location />
      <CongratulatoryMoney />
      <Footer
        style={{
          background: "#D7CCC8",
          backgroundImage: `url(${GroovePaper})`,
          opacity: 0.6,
          textAlign: "center",
        }}
        >
        From BrotherBae Junhyeong  
      </Footer>
      <br />
    </Wrapper>
    </main>

    </HelmetProvider>

  );
};

export default IndexPage;
